
.story {
  width: 100%;
  height: 100%;
  background: #eee;
  padding: 50px;
}
.story .item {
  height: 150px;
  width: 300px;
  border: 1px solid #999;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: start;
      -ms-flex-pack: start;
          justify-content: flex-start;
}
.story .item img {
  height: 100%;
  min-width: 100px;
}
.story .item h4 {
  font-size: 23px;
  color: #f00;
  padding: 5px 0;
}


